import $store from '@/store/index.js';
class websocket {
	constructor(arg) {
		this.timer = null
		this.url = arg.url
		this.isOnline = false
		this.socket = null
		this.reconnectErrorTime = 0
		this.reconnectClosedTime = 0
		this.isOpenReconnect = true
		this.loadFinish = false;
		this.retryTime = 10;
		this.isClosed = false;
		this.isError = false;
		// 
		let user = uni.getStorageSync('user')
		this.user_token = uni.getStorageSync('token')
		this.user = user ? user : {},
			this.TO = false;
		console.log("websocket!!!!!");
		// 
		if (this.user) {
			this.connectSocket()
		}
	}
	// 
	reconnect(type) {
		console.log(this.isOnline)
		console.log("reconnect")
		if (this.isOnline) {
			return
		}
		if (type === 1 && this.isError) {
			return;
		}
		if (this.reconnectClosedTime >= this.retryTime || this.reconnectErrorTime >= this.retryTime) {
			return this.reconnectConfirm()
		}
		if (type === 1) {
			this.reconnectClosedTime += 1
		}
		if (type === 2) {
			this.reconnectErrorTime += 1
		}
		console.log(": " + this.reconnectClosedTime)
		console.log(": " + this.reconnectErrorTime)
		this.connectSocket();

	}
	// socket
	connectSocket() {
		console.log("connectSocket")
		let that = this;
		this.socket = uni.connectSocket({
			url: this.url,
			complete: () => {}
		})
		// 
		this.socket.onOpen(() => {
			this.onOpen()


		})
		// 
		this.socket.onMessage((res) => this.onMessage(res))
		// 
		this.socket.onClose((e) => this.onClose(e))
		// 
		this.socket.onError(() => this.onError())
		uni.onSocketClose(function(res) {
			console.log('WebSocket ');
			that.isClosed = true;

		});

		this.socket.onClose((e) => {
			console.log(e);
			that.isClosed = true;
		})
	}
	sendMessage(message, callback) {
		if (!this.socket) {
			typeof callback === 'function' && callback(false, message);
			return;
		}
		this.socket.send({
			data: message,
			success() {
				console.log('success')
				typeof callback === 'function' && callback(true, message);
			},
			fail() {
				console.log('fail')
				typeof callback === 'function' && callback(false, message);
			}
		})
	}
	onOpen() {
		// 
		this.isOnline = true
		this.isError = false
		this.isClosed = false
		console.log('websocket')
		this.isOpenReconnect = true

	}
	onMessage(message) {
		// console.log(message.data);
		uni.$emit('onMessage', JSON.parse(message.data))
	}

	// 
	onClose(e) {
		// 
		this.isOnline = false
		this.socket = null
		this.isClosed = true;
		console.log('socket')
		if (this.isOpenReconnect) {
			console.log("3")
			var that = this;
			setTimeout(function() {
				that.reconnect(1);
			}, 3000);
		}
	}
	// 
	onError() {
		// 
		this.isOnline = false
		this.socket = null
		this.isError = true;
		console.log('socket')
		if (!this.socket && this.isOpenReconnect) {
			var that = this;
			setTimeout(function() {
				that.reconnect(2);
			}, 3000);
		}
	}
	// 
	close() {
		if (this.socket) {
			this.socket.close()
		}
		this.isOpenReconnect = false
		this.isClosed = true;
	}
	initSocketLogin(login) {
		this.socket.send({
			data: JSON.stringify(login),
			success() {
				console.log('IM:success')
			},
			fail(e) {
				console.log('IMfail:' + e)
			}
		})
	}
	// 
	reconnectConfirm() {
		// this.connectSocket()
		this.reconnectClosedTime = 0
		this.reconnectErrorTime = 0
	}
	// 
	checkOnline() {
		if (!this.isOnline) {
			// 
			this.reconnectConfirm()
			return false
		}
		return true
	}
	// 
	send(message, onProgress = false) {
		uni.sendSocketMessage({
			data: JSON.stringify(message),
			success: () => {

			},
			fail: () => {

			}
		})
	}
}

export default websocket
